import React, {Component} from "react";
import {connect} from "react-redux";
import {isSimfreeInstOfferType} from "../../selectors";

class SogProductListBannerView extends Component {
    constructor(props) {
        super(props);
    }

    toHTML(content) {
        return {__html: content};
    }

    render() {
        return this.props.isSimfreeInstOfferType && this.props.content ? (
            <div className="sog-banner-component u-spacing-l">
                <div dangerouslySetInnerHTML={this.toHTML(this.props.content)}/>
            </div>
        ) : null;
    }

}

const mapStateToProps = state => ({
    isSimfreeInstOfferType: isSimfreeInstOfferType(state)
});


const SogProductListBannerComponent = connect(mapStateToProps)(SogProductListBannerView);
export default SogProductListBannerComponent;
